export default {
	data() {
		return {
			verifyCodeText: '发送验证码',
			verifyStatus:true,
			focusArray: ['name', 'age', 'phone', 'verify'],
			focusIndex: null,  //焦点位置
			agreement:false, // 是否同意
			name: '',
			age: '',
			phone: '',
			verifyCode: ''
		};
	},
	methods:{
		agreementChange(){
			this.agreement = !this.agreement
			
		},
		handleBlur(index){
			let focusArray = this.focusArray
			if(index != focusArray.length - 1 ){
				if(!this[focusArray[index + 1]]){
					this.focusIndex = index + 1
					return
				}
				this.focusIndex = null
				return
			}
			this.focusIndex = null	
		},
		getVerifyCode(){
			if(!this.phone){
				uni.showToast({
					icon:'none',
					title:'请输入电话号码'
				})
				this.focusIndex = 2
				return
			}
			const phoneRule = "^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$"
			const correctPhone = this.phone.match(phoneRule)
			if(!correctPhone){
				uni.showToast({
					icon:'none',
					title:'手机号码错误，请确认'
				})
				this.focusIndex = 2
				return
			}
		},
		submit(){
			if(!this.name){
				uni.showToast({
					icon:'none',
					title:'请输入姓名'
				})
				this.$nextTick(()=>{
					this.focusIndex = 0
				})
				return
			}
			if(!this.age){
				uni.showToast({
					icon:'none',
					title:'请输入年龄'
				})
				this.$nextTick(()=>{
					this.focusIndex = 1
				})
				return
			}
			if(!this.phone){
				uni.showToast({
					icon:'none',
					title:'请输入电话号码'
				})
				this.$nextTick(()=>{
					this.focusIndex = 2
				})
				return
			}
		}
	}
}
